.full-screen-frame{
	width:calc(100vw - 80px);
	height:calc(100vh - 80px);
	display:flex;
	align-items:center;
	justify-content:center;
	border:1px solid #ccc;
	padding:50px;
	margin:30px;
	border-radius:5px;
	color:#666;
	box-shadow:0 0 10px rgba(#000, .1);
	h1{
		margin-bottom: 1em;
	}
}

h1, h2, h3, h4{
	font-family:$font-family-title;
	color:theme-color(primary);
}

header{
	position:sticky;
	padding:15px 0;
	button[aria-controls="primary-nav-bar"]{
		background:none;
		appearance:none;
		padding:10px;
		width: 50px;
		height: 40px;
		border-radius:5px;
		border:1px solid #999;
		position: relative;
		>div{
			transition: .3s all;
			position: relative;
			height: 100%;
		}
		span{
			transition: .3s all;
			display: block;
			border-top:2px solid theme-color("primary");
			border-bottom:2px solid theme-color("primary");
			position: absolute;
			margin-top: -2px;
			&:nth-child(1){
				top: 0;
				left: 0;
				right: 0;
			}
			&:nth-child(2){
				top: 50%;
				left: 0;
				right: 0;
			}
			&:nth-child(3){
				top: 100%;
				left: 0;
				right: 0;
			}
		}

		&:hover{
			>div{
				//transform: rotate(90deg);
			}
			span{
				&:nth-child(1){
					left: 15px;
				}
				&:nth-child(2){
					left: 5px;
				}
				&:nth-child(3){
					left: 10px;
				}
			}
		}

		.show-mobile-menu &{
			>div{
				transform: rotate(90deg);

			}
		}
	}
	.logo{
		font-family: $font-family-title;
		font-weight: bold;
		font-size:50px;
		text-decoration: none;
		line-height: 1;
	}

	@include media-breakpoint-up(menu){
		button[aria-controls="primary-nav-bar"]{
			display: none
		}
	}
}

#primary-nav{
	#primary-nav-bar{
		position: fixed;
		top: 100%;
		left: 0;
		right: 0;
		bottom:0;
		z-index: 1000;
		background: linear-gradient(rgba(#fff, .8), #fff);

		align-items:center;
		justify-content: center;
		font-family: $font-family-title;
		transition: .3s all;
		.show-mobile-menu &{
			top:80px;
			ul{
				height: 100%;
			}
		}
		ul{
			list-style: none;
			margin: 0;
			padding:0;
			display: flex;
			flex-direction: column;
			justify-content: center;
			text-align: center;
			li{
				margin-bottom: 1.5em;
			}
			
		}
		a{
			color: #000;
			font-weight: bold;
			font-size: 32px;
			opacity: .6;
			text-decoration: none;
			&:hover{
				opacity: 1;
			}
		}
	}

	@include media-breakpoint-up(menu){
		#primary-nav-bar{
			position: relative;
			background:#fff;
			display: block;
			ul{
				justify-content: space-between;
				flex-direction: row;
				width: 100%;
				li{
					margin-bottom: 0;
				}
			}
			a{
				color: theme-color(primary);
				font-size: 16px;
				opacity: 1;

			}
		}

	}
}
















